<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-input v-model="realName" placeholder=""></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-input v-model="tel_number" placeholder=""></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-radio v-model="role" label="Super_Admin"></el-radio>
          <el-radio v-model="role" label="Admin"></el-radio>
          <el-radio v-model="role" label="User"></el-radio>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()"></el-button>
        <el-button class="CoResetBtn" @click="goPrePage()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import SYSTEM from 'admin/service/system-service.js'
  export default {
    name: "AddAdministrators",
    data(){
      return{
        realName : '',
        tel_number : '',
        role: 'Admin'
      }
    },
    methods:{
      saveForm(){
        let data = {
          'realName' : this.realName,
          'tel_number' : this.tel_number,
          'role' : this.role
        };
        SYSTEM.setUserAdmin(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.$message({
                type: 'success',
                message: ''
              });
              this.goPrePage();
            }
          })
      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



